import React from "react";
import SendCode from "./SendCode";
import { useState, useEffect } from "react";
import axios from 'axios';
import { useHistory } from 'react-router-dom';

const Email = () => {
  const history = useHistory();
  const [error, setError] = useState(false);
  const [value, setValue] = useState("");
  const handleInput = (e) => {
    if (e.target.value.length === 50) {
      setError(true);
    } else {
      setError(false);
    }
    
    setValue(e.target.value);
  };

  const handleSubmit = async (e) => {
     history.replace('/verificationscreen');
    e.preventDefault();
    try {
      const res = await axios.post('http://54.171.172.119:3001/api/v1/auth/sendPinCode', {
        email: value,
        pinCode: "1234"
      })
      console.log(res.data)
    } catch (e) {
      console.log('error')
      console.log(e)
    }
  };

  return (
    <form
      onSubmit={(e) => handleSubmit(e)}
      className="flex flex-col relative w-full gap-2 h-full justify-between pt-[50%]"
    >
      <div>
        <p className="ml-4">Email</p>
        <input
          className="border-2 border-black px-3 py-1 rounded w-full"
          type="email"
          aria-label="Email"
          placeholder="email"
          required
          maxLength={50}
          onChange={(e) => {
            handleInput(e);
          }}
        />
      </div>
      {error && (
        <p className="absolute top-[50%] font-semibold rounded px-2 text-center">
          The maximum length of 50 characters is reached.
        </p>
      )}
      <SendCode />
    </form>
  );
};

export default Email;
