import { useState, useContext } from "react";
import { Link, useHistory,  } from "react-router-dom";
import axios from "axios";
import AuthContext from "../../../../store/auth-context";

// import AuthContext from "../../store/auth-context";
// AuthContextProvider

const AuthForm = () => {
    const history = useHistory();
    const [enteredEmail, setEnteredEmail] = useState("");
    const [enteredPassword, setEnteredPassword] = useState("");
    const [btnBgColor, setBtnBgColor] = useState("#F2385F");
    const [emailMsg, setEmailMsg] = useState("");
    const [passMsg, setPassMsg] = useState("");

    const authCtx = useContext(AuthContext);

    const [isLogin] = useState(true);
    const [isLoading, setIsLoading] = useState(false);
    const [formError, setFormError] = useState(null)

    const submitHandler = (event) => {
        event.preventDefault();
        setBtnBgColor('#000');

        // optional: Add validation
        if (enteredEmail.length >= 50) {
            setEmailMsg("The maximum length of 50 characters is reached.")
        } else {
            setEmailMsg("");
        }

        setIsLoading(true);
        setFormError(null)
        axios.post("http://54.171.172.119:3001/api/v1/auth/login", {
            email: enteredEmail,
            password: enteredPassword
        }).then(
            (response) => {
                console.log(response.data)
                setIsLoading(false);
                authCtx.login(response.data);
                history.replace("/");
           }).catch((err)=>{
                setIsLoading(false);
                // alert(err)
                const error = err.response.data 
                console.log(error)
                if(error && error.message && typeof error.message === 'string') { 
                    setFormError(error.message)
                }
            })
    };

    return (
        <section className="h-screen px-4 py-20">
            <h1 className="text-center my-5 text-2xl md:text-3xl tracking-tight font-extrabold">
                Security Links
            </h1>
            <form onSubmit={submitHandler} className="max-w-md mx-auto mt-5 md:mt-10">
                {formError && <p className="my-2 text-red-400">{formError}</p>}
                <div className="mb-6">
                    <label
                        htmlFor="email"
                        className="block mb-2 text-sm font-medium text-gray-900"
                    >
                        Email
                    </label>
                    <input
                        type="email"
                        id="email"
                        className="bg-gray-50 border border-gray-300 text-gray-900 text-base rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                        required
                        value={enteredEmail}
                        onChange={e => {setEnteredEmail(e.target.value)
                            if (enteredEmail.length >= 50) {
                                setEmailMsg("The maximum length of 50 characters is reached.")
                            } else {
                                setEmailMsg("");
                            }
                        }}
                        autoComplete={"off"}
                    />
                   {emailMsg.length > 0 ?  <p className="mt-2 text-sm text-red-600 dark:text-red-500">{emailMsg}</p>:""}
                </div>
                <div className="mb-6">
                    <label
                        htmlFor="password"
                        className="block mb-2 text-sm font-medium text-gray-900"
                    >
                        Password
                    </label>
                    <input
                        type="password"
                        id="password"
                        className="bg-gray-50 border border-gray-300 text-gray-900 text-base rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5"
                        required
                        value={enteredPassword}
                        onChange={e => {setEnteredPassword(e.target.value)
                            if (enteredPassword.length >= 50) {
                                setPassMsg("The maximum length of 50 characters is reached.")
                            } else if (enteredPassword.length < 6) {
                                setPassMsg("The password should contain at least 6 characters.");
                            } else {
                                setPassMsg("")
                            }
                        }}
                    />
                    {passMsg.length > 0 ?  <p className="mt-2 text-sm text-red-600 dark:text-red-500">{passMsg}</p>:""}
                </div>
                <div className="mb-6">
                    {!isLoading && (
                        <button style={{backgroundColor: btnBgColor}} className="w-full text-white bg-gray-800 hover:bg-gray-900 focus:outline-none focus:ring-4 focus:ring-gray-300 font-medium rounded-lg text-base px-5 py-2.5 mr-2 my-2">
                            {isLogin ? "Sign In" : "Create Account"}
                        </button>
                    )}
                    {isLoading && <p className="text-center">Signing you in ...</p>}
                    {/* <button
            type='button'
            onClick={switchAuthModeHandler}
          >
            {isLogin ? 'Create new account' : 'Login with existing account'}
          </button> */}
                </div>
                <div className="flex justify-end">
                    <p className="cursor-pointer text-sm font-medium text-gray-900">
                        <Link to={"/forgotpassword"}>
                            Forgot Password?
                        </Link>
                    </p>
                </div>
            </form>
        </section>
    );
};

export default AuthForm;
