import React from "react";

const ResendCode = ({ setMessage, setConfirmation }) => {
  const handleResend = () => {
    setMessage("Successfully sent verification code. Please check your email.");
    setConfirmation(true);
    setTimeout(() => {
      setConfirmation(false);
    }, 1000);
  };
  return (
    <button className="w-max self-end mr-[20px]" onClick={handleResend}>
      Resend Code
    </button>
  );
};

export default ResendCode;
