import React from 'react'
import { Link } from 'react-router-dom'
import IcBell from "../../../Homepagescreen/assets/icons/ic_bell.svg";
const Header = () => {
  return (
    <div className="w-11/12 ml-12">
            <div className="flex justify-between items-center">
            <div>
              <h2 className="text-5xl font-bold mb-4">Welcome Bellrock!</h2>
              <p className="font-bold">Here is your dashboard </p>
            </div>
            <div className="flex items-center space-x-4">
              
              <img className="w-6 h-6" src={IcBell} />
              <Link to={"/profile"}>
              <img
                className="w-10 h-10 rounded-full"
                src="https://i.pravatar.cc/300"
                alt="Rounded avatar"
                />
                </Link>
        </div>
      </div>
      </div>
  )
}

export default Header
