import { createContext, useContext } from "react";
import { Switch, Route, Redirect } from "react-router-dom";

import Layout from "./components/Layout/Layout";
// import UserProfile from "./components/Profile/UserProfile";
import AuthPage from "./screens/Authscreen/pages/AuthPage";
import HomePage from "./screens/Homepagescreen/pages/HomePage";
import Profile from "./screens/ProfileScreen/profile"
// import RegisterPage from "./screens/Registerpage/pages/RegisterPage";
import AuthContext from "./store/auth-context";
import ForgotPassword from "./screens/Forgotpasswordscreen/pages/ForgotPassword";
import ForgotPassword1 from "./screens/Forgotpasswordscreen/pages/ForgotPassword1";
import VerificationScreen from "./screens/Verificationscreen/pages/VerificationScreen";
import AddUser from "./screens/staff/addUser/AddUser";

// import ProfilePage from "./screens/Profilepage/pages/ProfilePage";

function App() {
    const authCtx = useContext(AuthContext); 

    return (
        <Layout>
            <Switch>
                <Route path="/" exact>
                    {authCtx.isLoggedIn && <HomePage />}
                    {!authCtx.isLoggedIn && <Redirect to="/login" />}
                </Route>
                <Route path="/forgotpassword">
                    <ForgotPassword1 />
                </Route>
                <Route path="/updatepassword">
                    <ForgotPassword />
                </Route>
                
                <Route path="/verificationscreen">
                    <VerificationScreen />
                 </Route>
                {!authCtx.isLoggedIn && (
                    <Route path="/login">
                        <AuthPage />
                    </Route>
                )}
                {/* {!authCtx.isLoggedIn && (
                    <Route path="/register">
                        <RegisterPage />
                    </Route>
                )} */}
                <Route path="/profile"> 
                    {authCtx.isLoggedIn && <Profile />}
                    {!authCtx.isLoggedIn && <Redirect to="/login" />}
                </Route>
                <Route path="/staff/add">
                    {authCtx.isLoggedIn && <AddUser />}
                    {!authCtx.isLoggedIn && <Redirect to="/login" />}
                </Route>
                {/* <Route path="/template">
                    <DashboardTemplate 
                        content={<h1>hello worlx</h1>} 
                        centerContent={false}
                        activeItemId={2}
                    />
                </Route> */}
                <Route path="*">
                    <Redirect to="/" />
                </Route>
            </Switch>
        </Layout>
    );
}

export default App;
