import Dashboard from './pages/Dashboard';
import Header from './component/Header/Header'
import Sidebar from '../Homepagescreen/components/Sidebar';

function Profile() {
  return (
      <div className='flex flex-row flex-nowrap page-root px-8 , py-6'>
        <Sidebar activeItemId={1} />
        <div className="w-11/12">
          <Header />
          <Dashboard className='px-8 , py-8' />
        </div>
      </div>
  );
}

export default Profile;
