import { useState, useEffect } from "react";
import OTPInput, { ResendOTP } from "otp-input-react";
import VerifyButton from "./VerifyButton";
import ResendCode from "./ResendCode";
import { useHistory } from 'react-router-dom';



const Otp = () => {
  const history = useHistory();
  const [OTP, setOTP] = useState("");
  const [error, setError] = useState(false);
  const [message, setMessage] = useState("");
  const [countDown, setCountDown] = useState(0);
  const [confirmation, setConfirmation] = useState(false);

  const handleClick = async (e) => {
    if (OTP.length < 1) {
      setMessage("You have entered an invalid code.");
      setError(true);
      setTimeout(() => {
        setError(false);
      }, 1000);
    } else if (countDown === 0 || countDown === 1) {
      setMessage(
        "The email passcode you entered has expired. Please press the resend code button toget the new verification code and then try again."
      );
      setError(true);
      setTimeout(() => {
        setError(false);
      }, 1000);
    }
    await fetch("http://54.171.172.119:3001/api/v1/auth/sendPinCode", {
      method: "POST",
      mode: "no-cors",
      body: JSON.stringify({
        email: "leo8546@dev.com",
        pinCode: "1234",
      }),
    })
      .then()
      .catch((error) => console.log(error));
  };

  useEffect(() => {
    let timerId;

    setCountDown(60 * 30);
    timerId = setInterval(() => {
      setCountDown((countDown) => countDown - 1);
    }, 1000);

    return () => {
      clearInterval(timerId);
    };
  }, []);

  useEffect(() => {
    if (countDown < 0) {
      setCountDown(0);
    }
  }, [countDown]);

  return (
    <div className="flex flex-col w-full gap-2 h-full justify-between pt-[50%]">
      <div>
        <div className="flex flex-col gap-2">
          <OTPInput
            value={OTP}
            onChange={setOTP}
            OTPLength={4}
            otpType="number"
            autoFocus
            disabled={false}
            inputStyles={{
              width: "80px",
              height: "90px",
              fontSize: "1.5rem",
              border: "2px solid black",
              borderRadius: "5px",
              fontWeight: "bold",
            }}
          />
          <ResendCode
            setMessage={setMessage}
            setConfirmation={setConfirmation}
          />
        </div>
        {error && (
          <div className="absolute top-3 left-[50%] text-center -translate-x-[50%] w-56 h-28 p-2 bg-black text-white rounded-lg shadow-xl flex items-center justify-center gap-3 text-sm font-semibold">
            {message}
          </div>
        )}
        {confirmation && (
          <div className="absolute top-[50%] left-[50%] text-center -translate-x-[50%] -translate-y-[50%] w-56 h-28 p-2 bg-black text-white rounded-lg shadow-xl flex items-center justify-center gap-3 text-sm font-semibold">
            {message}
          </div>
        )}
      </div>
      <VerifyButton click={handleClick} />
    </div>
  );
};

export default Otp;
