import React from "react";
import { Link } from "react-router-dom";

const VerifyButton = ({ click }) => {
  return (
    <button className="bg-[#f2385f] text-white py-2 rounded" onClick={click}>
      <Link to={"/updatepassword"}>
        Verify
      </Link>
     
    </button>
  );
};

export default VerifyButton;
